<template>
  <v-app>
    <v-toolbar app color="primary">
      <v-toolbar-title v-t="'title'" class="hidden-xs-only"></v-toolbar-title>
      <v-toolbar-title v-t="'short_title'" class="hidden-sm-and-up"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu offset-y>
        <v-btn icon slot="activator">
          <v-icon>language</v-icon>
        </v-btn>
        <v-list>
          <v-list-tile @click="language = 'ja'">
            <v-list-tile-title v-t="'languages.ja'"></v-list-tile-title>
          </v-list-tile>
          <v-list-tile @click="language = 'en'">
            <v-list-tile-title v-t="'languages.en'"></v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-btn icon @click.stop="refreshFiat()">
        <v-icon>refresh</v-icon>
      </v-btn>
      <v-btn icon v-if="!usingCamera" @click.stop="usingCamera = true">
        <v-icon>camera_alt</v-icon>
      </v-btn>
      <v-btn icon v-if="usingCamera" @click.stop="usingCamera = false">
        <v-icon>clear</v-icon>
      </v-btn>
      <v-btn icon @click.stop="help = true">
        <v-icon>help</v-icon>
      </v-btn>
    </v-toolbar>
    <v-content>
      <div>
        <v-alert
          v-model="errorMessage"
          type="error"
          v-t="'error.unknown'"
          dismissible
        >
        </v-alert>
      </div>
      <v-dialog v-model="errorDialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span>{{ errorDialogMessage }}</span>
          </v-card-title>
          <v-card-actions>
            <v-btn color="primary" flat @click.stop="errorDialog=false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog
        v-model="help"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
        scrollable
      >
        <v-card tile>
          <v-toolbar card dark color="primary">
            <v-toolbar-title v-t="'help.title'"></v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon dark @click.native="help = false">
              <v-icon>close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-card-text>
            <v-list three-line subheader>
              <v-list-tile avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-t="'help.creator'"></v-list-tile-title>
                  <v-list-tile-sub-title v-t="'help.creator_name'"></v-list-tile-sub-title> </v-list-tile-content>
              </v-list-tile>
            </v-list>
            <v-list three-line subheader>
              <v-subheader v-t="'help.how_to'"></v-subheader>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-icon>language</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title v-t="'help.how_to_language'"></v-list-tile-title>
                  <v-list-tile-sub-title v-t="'help.how_to_language_detail'"></v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-icon>refresh</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title v-t="'help.how_to_refresh'"></v-list-tile-title>
                  <v-list-tile-sub-title v-t="'help.how_to_refresh_detail'"></v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-icon>camera_alt</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title v-t="'help.how_to_camera'"></v-list-tile-title>
                  <v-list-tile-sub-title v-t="'help.how_to_camera_detail'"></v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile avatar>
                <v-list-tile-action>
                  <v-icon>help</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title v-t="'help.how_to_help'"></v-list-tile-title>
                  <v-list-tile-sub-title v-t="'help.how_to_help_detail'"></v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-card-text>

          <div style="flex: 1 1 auto;"></div>
        </v-card>
      </v-dialog>
      <v-layout row>
        <v-flex xs12 sm6 offset-sm3>
          <qrcode-reader
            v-if="usingCamera"
            @init="onCameraInit"
            @decode="onCameraDecode"
            :paused="paused"
          ></qrcode-reader>
          <v-card v-if="!qrcodeDataUri">
            <v-card-title primary-title>
              <div>
                <div class="headline" v-t="'loading'"></div>
              </div>
            </v-card-title>
          </v-card>
          <v-card v-if="qrcodeDataUri && !usingCamera">
            <v-card class="result">
              <div class="qrcode-info">
                <div class="qrcode-area">
                  <img :src="qrcodeDataUri" alt="QRCode" id="qrcode"/>
                  <div
                    v-if="characterIconPath"
                    id="character"
                    :style="{'background-image':'url('+characterIconPath+')'}"
                  ></div>
                </div>
                <div class="amount_viewer" v-if="checkAmountNumber() && amount > 0">
                  <p v-t="{path: 'amount_view', args: {amount: useFiat ? fiat : amount, amount_type: useFiat ? amountType : 'VIPS'}}"></p>
                </div>
                <div class="uri_viewer">
                  <p><a :href="uri">{{ uri }}</a></p>
                </div>
                <div class="notice" v-if="useFiat && checkAmountNumber() && amount > 0">
                  <p v-t="'proprietary_spec'"></p>
                </div>
              </div>
            </v-card>
            <v-container>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                    id="address"
                    name="address"
                    :label="$t('address')"
                    v-model="address"
                   :rules="[
                      () => checkValidAddress() || $t('error.required_valid_address')
                    ]"
                    maxlength="34"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-subheader v-t="'icon'"></v-subheader>
              </v-layout>
              <v-layout row>
                <v-layout wrap justify-space-around align-center>
                  <v-avatar :color="character === 'none' ? 'primary' : ''">
                    <v-icon @click="character = 'none'" class="character_icon">block</v-icon>
                  </v-avatar>
                  <v-avatar :color="character === 'coin' ? 'primary' : ''">
                    <img src="./assets/coin.png" @click="character = 'coin'" class="character_icon" />
                  </v-avatar>
                  <v-avatar :color="character === 'kaname' ? 'primary' : ''">
                    <img src="./assets/kaname.png" @click="character = 'kaname'" class="character_icon" />
                  </v-avatar>
                  <v-avatar :color="character === 'iroha' ? 'primary' : ''">
                    <img src="./assets/iroha.png" @click="character = 'iroha'" class="character_icon" />
                  </v-avatar>
                  <v-avatar :color="character === 'moyashi' ? 'primary' : ''">
                    <img src="./assets/moyashi.png" @click="character = 'moyashi'" class="character_icon" />
                  </v-avatar>
                  <v-avatar :color="character === 'tereko' ? 'primary' : ''">
                    <img src="./assets/tereko.png" @click="character = 'tereko'" class="character_icon" />
                  </v-avatar>
                </v-layout>
              </v-layout>
              <v-layout row>
                <v-layout>
                  <div class="character-name">
                    <p v-if="character === 'none'" v-t="'characters.none'"></p>
                    <p v-if="character === 'coin'" v-t="'characters.coin'"></p>
                    <p v-if="character === 'kaname'" v-t="'characters.kanade'"></p>
                    <p v-if="character === 'iroha'" v-t="'characters.iroha'"></p>
                    <p v-if="character === 'moyashi'" v-t="'characters.moyashi'"></p>
                    <p v-if="character === 'tereko'" v-t="'characters.tereko'"></p>
                  </div>
                </v-layout>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-select
                    :items="fiatList"
                    v-model="amountType"
                    :label="$t('fiat_list')"
                    autocomplete
                  ></v-select>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-switch
                    :label="$t('use_fiat', {amount_type: useFiat ? amountType : 'VIPS'})"
                    v-model="useFiat"
                  ></v-switch>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs18>
                  <v-text-field
                    id="fiatRate"
                    name="fiatRate"
                    :label="$t('rate')"
                    v-model="fiatRate"
                    type="number"
                   :rules="[
                      () => checkFiatRateNumber() || $t('error.required_number')
                    ]"
                  ></v-text-field>
                </v-flex>
                <v-flex xs4>
                  <v-subheader>VIPS/{{ amountType }}</v-subheader>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field
                    id="amount"
                    name="amount"
                    :label="$t('amount')"
                    v-model="amount"
                    @focus="onAmount"
                    type="number"
                   :rules="[
                      () => checkAmountNumber() || $t('error.required_number')
                    ]"
                  ></v-text-field>
                </v-flex>
                <v-flex xs2>
                  <v-subheader>VIPS</v-subheader>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field
                    id="fiat"
                    name="fiat"
                    :label="$t('fiat')"
                    v-model="fiat"
                    @focus="onFiat"
                    type="number"
                    :rules="[
                      () => checkFiatNumber() || $t('error.required_number')
                    ]"
                  ></v-text-field>
                </v-flex>
                <v-flex xs2>
                  <v-subheader>{{ amountType }}</v-subheader>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field
                    id="message"
                    name="message"
                    :label="$t('message')"
                    v-model="message"
                    maxlength="40"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </v-content>
  </v-app>
</template>

<style>
.result {
  padding-top: 1em;
  padding-bottom: 3em;
}

div.qrcode-area {
  margin: 0 auto;
  text-align: center;
}

div.qrcode-area {
  width: 200px;
  height: 200px;
  position: relative;
}

#qrcode {
  width: 100%;
  height: 100%;
}

#character {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 80px;
  left: 80px;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

div.character-name {
  width: 100%;
  text-align: center;
}

img.character_icon {
  width: 40px;
  height: 40px;
}

div.amount_viewer,
div.uri_viewer,
div.notice {
  width: 100%;
  text-align: center;
}
div.amount_viewer p,
div.notice p {
  font-weight: bold;
  font-size: 1.5em;
}
</style>

<script>
import axios from 'axios'
import BigNumber from 'bignumber.js'
import bip21 from 'bip21'
import qrcode from 'qrcode'

import {
  address
} from 'bitcoinjs-lib'

const TICKER = {
  ID: 2688,
  URL: 'https://api.coinmarketcap.com/v2/ticker/'
}
const BIP21URN = 'vipstarcoin'

const vipsCoinIcon = require('./assets/coin.png')
const kanameIcon = require('./assets/kaname.png')
const irohaIcon = require('./assets/iroha.png')
const moyashiIcon = require('./assets/moyashi.png')
const terekoIcon = require('./assets/tereko.png')

export default {
  data () {
    return {
      address: 'VUmino3VbniKFyCzgYwpt8ZWfWumLGzSX4',
      character: 'coin',
      characterIconPath: vipsCoinIcon,
      qrcodeDataUri: '',
      uri: '',
      amount: '',
      fiat: '',
      message: '',
      focused: '',
      fiatRate: '0',
      errorMessage: false,
      usingCamera: false,
      paused: false,
      help: false,
      language: 'ja',
      errorDialog: false,
      errorDialogMessage: '',
      useFiat: false,
      amountType: 'JPY',
      fiatList: [
        'AUD',
        'BRL',
        'CAD',
        'CHF',
        'CLP',
        'CNY',
        'CZK',
        'DKK',
        'EUR',
        'GBP',
        'HKD',
        'HUF',
        'IDR',
        'ILS',
        'INR',
        'JPY',
        'KRW',
        'MXN',
        'MYR',
        'NOK',
        'NZD',
        'PHP',
        'PKR',
        'PLN',
        'RUB',
        'SEK',
        'SGD',
        'THB',
        'TRY',
        'TWD',
        'USD',
        'ZAR'
      ]
    }
  },
  mounted: function () {
    if (window.location.host.indexOf('github.io') > -1 && window.location.protocol === 'http:') {
      window.location.protocol = 'https'
    }
    this.refreshFiat()
  },
  methods: {
    refreshFiat: function () {
      this.qrcodeDataUri = ''
      this.getFiat().then(price => {
        this.fiatRate = price
        this.updateQRCode()
      }).catch(error => {
        this.errorMessage = true
        console.error(error)
      })
    },
    updateQRCode: function () {
      if (
        !this.checkValidAddress(this.address) ||
        !this.checkAmountNumber()
      ) {
        return
      }
      let opt = {}
      if (this.message !== '') opt.message = this.message
      if (this.useFiat && this.fiat !== '' && this.fiat !== '0') {
        opt.amountExt = this.fiat
        opt.amountType = this.amountType
      } else if (this.amount !== '' && this.amount !== '0') {
        opt.amount = this.amount
      }
      this.uri = bip21.encode(this.address, opt, BIP21URN)
      return new Promise((resolve, reject) => {
        qrcode.toDataURL(
          this.uri,
          {
            errorCorrectionLevel: 'Q',
            type: 'image/png'
          },
          (err, uri) => {
            if (err !== null) {
              reject(err)
            } else {
              resolve(uri)
            }
          }
        )
      }).then(uri => {
        return new Promise((resolve, reject) => {
          this.qrcodeDataUri = uri
          resolve()
        })
      })
    },
    onAmount: function () {
      this.focused = 'amount'
    },
    onFiat: function () {
      this.focused = 'fiat'
    },
    checkValidAddress: function () {
      if (this.address[0] !== 'V') {
        return false
      }
      try {
        address.fromBase58Check(this.address)
        return true
      } catch (e) {
        return false
      }
    },
    checkFiatRateNumber: function () {
      if (this.fiatRate.match(/^([1-9]\d*|0)(\.\d+)?$/) || this.fiatRate === '') {
        return true
      }
      return false
    },
    checkAmountNumber: function () {
      if (this.amount.match(/^([1-9]\d*|0)(\.\d+)?$/) || this.amount === '') {
        return true
      }
      return false
    },
    checkFiatNumber: function () {
      if (this.fiat.match(/^([1-9]\d*|0)(\.\d+)?$/) || this.fiat === '') {
        return true
      }
      return false
    },
    getFiat: function () {
      let uri = `${TICKER.URL}${TICKER.ID}/?convert=${this.amountType}`
      return axios.get(uri).then(resp => {
        let quote = resp.data.data.quotes[this.amountType]
        if (quote === null) {
          return Promise.reject(new Error(`${this.amountType} does'nt supported`))
        } else {
          let price = new BigNumber(quote.price)
          return Promise.resolve(price.toString())
        }
      }).catch(error => {
        return Promise.reject(error)
      })
    },
    onCameraInit: function (promise) {
      promise.then(() => {
        this.paused = false
      }).catch(error => {
        this.paused = true
        this.usingCamera = false
        console.error(error)
        this.errorDialog = true
        if (error.name === 'NotAllowedError') {
          // user denied camera access permisson
          this.errorDialogMessage = error.message
        } else if (error.name === 'NotFoundError') {
          // no suitable camera device installed
          this.errorDialogMessage = error.message
        } else if (error.name === 'NotSupportedError') {
          // page is not served over HTTPS (or localhost)
          this.errorDialogMessage = error.message
        } else if (error.name === 'NotReadableError') {
          // maybe camera is already in use
          this.errorDialogMessage = error.message
        } else if (error.name === 'OverconstrainedError') {
          // passed constraints don't match any camera. Did you requested the front camera although there is none?
          this.errorDialogMessage = error.message
        } else {
          // browser is probably lacking features (WebRTC, Canvas)
          this.errorDialogMessage = error.message
        }
      })
    },
    onCameraDecode: function (content) {
      try {
        let decode = bip21.decode(content, BIP21URN)
        this.address = decode.address
        if (decode.options.message) this.message = decode.options.message
        if (decode.options.amountExt && decode.options.amountType) {
          this.fiat = decode.options.amountExt
          this.useFiat = true
          this.amountType = decode.options.amountType
          let fiat = new BigNumber(decode.options.amountExt)
          let amount = fiat.dividedBy(this.fiatRate)
          if (amount.toString() === 'NaN') {
            this.amount = '0'
          } else {
            this.amount = amount.decimalPlaces(8).toString()
          }
        } else if (decode.options.amount) {
          this.amount = decode.options.amount
        }
      } catch (error) {
        console.error(error)
        this.errorDialog = true
        this.errorDialogMessage = error.message
      }
      this.paused = true
      this.usingCamera = false
    }
  },
  watch: {
    language: function () {
      this.$i18n.locale = this.language
    },
    address: function () {
      this.updateQRCode()
    },
    amountType: function () {
      this.refreshFiat()
    },
    useFiat: function () {
      this.updateQRCode()
    },
    amount: function () {
      if (this.focused !== 'amount') {
        return
      }
      let amount = new BigNumber(this.amount)
      let fiat = amount.multipliedBy(this.fiatRate)
      if (fiat.toString() === 'NaN') {
        this.fiat = ''
      } else {
        this.fiat = fiat.toString()
      }
      if (this.fiat === '0') {
        this.fiat = ''
      }
      this.updateQRCode()
    },
    fiat: function () {
      if (this.focused !== 'fiat') {
        return
      }
      let fiat = new BigNumber(this.fiat)
      let amount = fiat.dividedBy(this.fiatRate)
      if (amount.toString() === 'NaN') {
        this.amount = ''
      } else {
        this.amount = amount.decimalPlaces(8).toString()
      }
      if (this.amount === '0') {
        this.amount = ''
      }
      this.updateQRCode()
    },
    message: function () {
      this.updateQRCode()
    },
    character: function () {
      switch (this.character) {
        case 'none':
          this.characterIconPath = null
          break
        case 'coin':
          this.characterIconPath = vipsCoinIcon
          break
        case 'kaname':
          this.characterIconPath = kanameIcon
          break
        case 'iroha':
          this.characterIconPath = irohaIcon
          break
        case 'moyashi':
          this.characterIconPath = moyashiIcon
          break
        case 'tereko':
          this.characterIconPath = terekoIcon
          break
      }
    }
  }
}
</script>



